"use client";
import { useState } from "react";
import "./globals.css";
import { FiMoon } from "react-icons/fi";
import { IoIosSearch } from "react-icons/io";
import { FiMapPin } from "react-icons/fi";
import styled from "styled-components";
import axios from "axios";

const Form = styled.form`
  display: flex;
  border: 1px solid white;
  border-radius: 5px;
  overflow: hidden;
  color: black;
  background-color: white;
  width: 40%;
`;

const Input = styled.input`
  flex: 1;
  margin: 10px;
  border: none;
  outline: none;
  font-size: 1rem;
  &::placeholder {
    color: black;
  }
`;

const Button = styled.button`
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background: transparent;
  cursor: pointer;
`;

const Page = () => {
  const [searchQuery, setSearchQuery] = useState("");
  const [suggestions, setSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);

  const handleInputChange = (e) => {
    const value = e.target.value;
    setSearchQuery(value);

    // try {
    //   await;
    // } catch (err) {}
    if (value.length > 2) {
      // Mock suggestions
      setSuggestions([
        { name: "London, United Kingdom", id: 1 },
        { name: "Los Angeles, USA", id: 2 },
        { name: "Lisbon, Portugal", id: 3 },
      ]);
      setShowSuggestions(true);
    } else {
      setShowSuggestions(false);
    }
  };

  const handleSearch = () => {
    console.log("show");
  };

  return (
    <>
      {/* Header */}
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-evenly",
        }}
      >
        <div>
          <h1>Weather</h1>
          <p>Search for a city to view current weather conditions</p>
        </div>
        <div>
          <button
            style={{
              borderRadius: "50%",
              border: "1px",
              borderColor: "white",
              cursor: "pointer",
            }}
            onClick={() => console.log("hello toggle mode")}
          >
            <FiMoon size={24} />
          </button>
        </div>
      </div>
      {/* Search bar*/}
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          marginTop: "1rem",
          flexDirection: "column",
          gap: "1rem",
        }}
      >
        <Form>
          <Input
            id="title"
            name="title"
            placeholder="Search for a location..."
            value={searchQuery}
            onChange={handleInputChange}
          />
          <Button type="submit" aria-label="Search">
            <IoIosSearch size={26} />
          </Button>
        </Form>

        {showSuggestions && suggestions.length > 0 && (
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              width: "40%",
              marginTop: "-0.5rem",
            }}
          >
            {suggestions.map((sug) => (
              <button
                style={{
                  borderColor: "white",
                  border: "1px",
                  textAlign: "start",
                  height: "2rem",
                  cursor: "pointer",
                }}
                key={sug.id}
                onClick={() => {
                  setSearchQuery(sug.name);
                  handleSearch();
                }}
              >
                <div>
                  <FiMapPin />
                  <span>{sug.name}</span>
                </div>
              </button>
            ))}
          </div>
        )}
      </div>
    </>
  );
};

export default Page;
